.mat-primary .mat-pseudo-checkbox-checked, .mat-primary .mat-pseudo-checkbox-indeterminate{
    background-color: transparent !important;
    border: 2px solid;
}

.mat-pseudo-checkbox.mat-pseudo-checkbox-checked, .mat-pseudo-checkbox.mat-pseudo-checkbox-indeterminate{
    border-color: #00467A !important
}

.mat-primary .mat-option.mat-selected:not(.mat-option-disabled){
    color: #00467A !important;
}

.mat-pseudo-checkbox{
    border: 2px solid #00467A !important;
    width: 15px !important;
    height: 15px !important;
}

.mat-pseudo-checkbox::after{
    color: #00467A !important;
    top: -2px !important;
    left: 2px !important;
    width: 12px !important;
    height: 5px !important;
    transition: none !important;
}

.mat-pseudo-checkbox-checked::before{
    content: url("assets/img/square.svg") !important;
    position: relative;
    top: -10px;
    width: 0px;
    left: 3px;
}

.mat-option:hover:not(.mat-option-disabled), .mat-option:focus:not(.mat-option-disabled), .mat-option.mat-active{
    background: transparent !important;
}

.multiselect-item-checkbox input[type=checkbox]:checked + div:before{
    background: transparent !important;
}

.multiselect-item-checkbox input[type=checkbox] + div:before{
    border: 2px solid #00467A !important;
    width: 12px !important;
    height: 12px !important;
    transform: rotate(269deg);
}

.multiselect-item-checkbox input[type=checkbox]:checked + div:before{
    content: url("assets/img/square.svg") !important;
}

.multiselect-item-checkbox input[type=checkbox] + div:after{
    border-color: #00467A !important;
    top: 5px !important;
    width: 12px !important;
    height: 4px !important;
    border-width: 0 0 2px 2px !important
}

.multiselect-item-checkbox:hover{
    background-color: transparent !important;
}

